<template>
  <div class="classification" :style="{ 'min-height': appHeight - 56 + 'px' }">
    <div class="header">
      <!--<img src="../../assets/image/icon-close2.png" width="28" height="28">-->
      <div class="title">{{lang('分类搜索')}}</div>
    </div>
    <div class="class-list" :style="{ 'min-height': appHeight - 100 + 'px' }">
      <div class="mdui-col-xs-4 side-bar1" :style="{ 'min-height': appHeight - 90 + 'px' }">
        <router-link to="/home" class="mdui-col-xs-12">{{lang('热门')}}</router-link>
        <a
          href="javascript:;"
          v-for="(firstList,index) of firstLists"
          :key="index"
          class="mdui-col-xs-12"
          @click="getClassInfo(firstList.id)"
          :class="firstId === firstList.id ? 'active' : ''"
        >{{firstList.name}}</a>
      </div>
      <div class="mdui-col-xs-8 side-bar2" :style="{ 'min-height': appHeight - 90 + 'px' }">
        <div
          class="tip"
          v-show="!menuList"
          :style="{ 'height': appHeight - 90 + 'px' }"
        >{{lang('请选择左侧类目')}}</div>
        <div
          class="mdui-col-xs-4 mdui-m-b-1"
          v-for="(classItem,index) in classList"
          :key="index"
          v-show="menuList"
        >
          <router-link
            :to="`/class/${firstId}/menu/${classItem.id}`"
            @click="goMenu(classItem.id)"
            class="second-class"
          >
            <img :src="classItem.class_img?config.cdnbase+classItem.class_img:''" class="class-img">
            <span class="class-title">{{classItem.name}}</span>
          </router-link>
        </div>
      </div>
    </div>
    <footer-nav/>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      appHeight: window.innerHeight,
      firstLists: [],
      classList: [],
      firstId: "",
      menuList: false
    };
  },
  async created() {
    this.firstLists = this.classInfo;
  },
  methods: {
    getClassInfo(id) {
      this.menuList = true;
      this.firstId = id;
      let list = this.firstLists.filter(item => {
        return item.id == id;
      });
      this.classList = list[0].subclass;
      this.menuList = true;
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  }
};
</script>

<style scoped lang="stylus">
  .classification
    background-color: #fff
    .mdui-col-xs-4,.mdui-col-xs-8
      text-align center
      /*height:100%*/
      .class-title
        overflow: hidden
        text-overflow: ellipsis
        white-space: nowrap;
    .header
      max-width: 750px;
      margin: 0 auto;
      border-bottom: 1px solid #EDEDED;
      overflow: hidden;
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      z-index: 1000;
      background-color: #fff;
      .title
        position: relative;
        height: 40px;
        line-height: 40px;
        color: #7B7B7B;
        text-align center
        padding: 0 8px;
        box-shadow: 0 1px 1px 0 rgba(237,237,237,1)
        background-color: #fff
    .class-list
      padding-top: 40px
      position relative
      .side-bar1
        position: absolute;
        left: 0;
        top: 40px;
        bottom: 0;
        width: 110px;
        z-index: 2;
        overflow-y auto
        padding-bottom 50px
      .side-bar2
        position: absolute;
        left: 110px;
        right: 0;
        top: 40px;
        bottom: 0;
        z-index: 2;
        width: auto;
        overflow-y auto
      &::after
        display: block
        content: ''
        clear: both
      &>.mdui-col-xs-4
        border-right: 1px solid #F2F2F2
        .mdui-col-xs-12
          display: block
          background: #fff;
          margin: 16px auto;
          box-sizing: border-box;
          width: 90%;
          height: 28px;
          line-height 28px
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap
          border-radius 18px
          &.active
            background: #B4272D;
            color: #FFFFFF
      .mdui-col-xs-8
        .tip
          display: flex
          justify-content center
          align-items center
          color: #777
        .mdui-col-xs-4.mdui-m-b-1
          min-width: min-content
          padding: 0 8px
  @media (min-width: 768px){
    .class-img {
      width: 132px
      height: 132px
      display block
      margin 0 auto 5px
    }
  }
  @media (max-width: 414px){
    .class-img {
      width: 54px
      height: 54px
      display block
      margin 0 auto 5px
    }
  }
  .second-class{
    margin-top: 15px;
    display: inline-block;
  }
</style>
