            {{detail.price-(detail.time_price?detail.time_price:0)}}
          </div>
          <div class="sell">{{detail.saleno}}{{lang('件已出售')}}</div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {

    };
  },
  props: ["detail", "pid"],
  computed: {
    ...mapState(["language","config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  }
};
