<template>
  <div class="menu-list">
    <div class="menu-show" v-show="!isShow">
      <div class="mdui-appbar mdui-appbar-fixed">
        <header-top :kfshow.sync="kfshow">
          <img
            width="28"
            height="28"
            src="../../assets/image/icon-menu.png"
            alt="menu"
            class="icon-menu"
            slot="menu"
            @click="showMenu"
          >
          <router-link to="/home" slot="title" class="title-img">
            <img :src="logo?config.cdnbase+logo:''" alt="title" height="19">
          </router-link>
          <img
            width="28"
            height="28"
            src="../../assets/image/logo.png"
            alt="logo"
            class="logo"
            slot="home"
            @click="kfShow()"
          >
        </header-top>
        <div class="mdui-tab mdui-color-theme">
          <a
            href="javascript:;"
            @click="getProduct(classItem.id)"
            class="mdui-ripple mdui-ripple-white"
            :class="$route.params.mid == classItem.id ? 'active' : ''"
            v-for="(classItem,index) of classList"
            :key="index"
          >{{classItem.name}}</a>
        </div>
      </div>
      <div class="mdui-container mdui-page-container mdui-m-t-1">
        <!--商品组件-->
        <product
          v-for="(product,index) of productList"
          :key="index"
          :detail="product"
          class="mdui-m-b-1"
          :pid="product.pid"
          @click="isShow=false"
        />
      </div>
      <min-footer v-show="true"/>
    </div>
    <class-list v-if="isShow" :is-show="isShow" @showMenu="isShow=false" :first-list="firstLists"/>
  </div>
</template>

<script>
import product from "../../components/commen/product.vue";
import { classPage } from "../../lib/goods";
import { mapState } from "vuex";
export default {
  data() {
    return {
      isActive: 0,
      productList: [],
      classList: [],
      isShow: false,
      isLoad: true,
      firstLists: "",
      kfshow: false,
      logo: this.$store.state.siteInfo.logo
    };
  },
  async created() {
    this.getMenuInfo();
  },
  methods: {
    kfShow() {
      this.kfshow = !this.kfshow;
    },
    async getMenuInfo() {
      this.firstLists = this.classInfo;
      this.productList = await classPage.get_class_product({
        id1: this.$route.params.id,
        id2: this.$route.params.mid
      });
      this.classList = this.firstLists.filter(item => {
        return item.id == this.$route.params.id;
      })[0].subclass;
    },
    showMenu() {
      this.isShow = true;
    },
    closeMenu() {
      this.isShow = true;
    },
    getProduct(mid) {
      this.$router.push(`/class/${this.$route.params.id}/menu/${mid}`);
    },
    async getProductAll() {
      this.productList = await classPage.get_class_product({
        id1: this.$route.params.id
      });
      this.isLoad = true;
      document.documentElement.scrollTop = 0;
    }
  },
  components: {
    product
  },
  watch: {
    async $route(to) {
      this.classList = this.firstLists.filter(item => {
        return item.id == this.$route.params.id;
      })[0].subclass;
      this.productList = await classPage.get_class_product({
        id1: to.params.id,
        id2: to.params.mid
      });
      document.documentElement.scrollTop = 0;
      this.isLoad = true;
    }
  },
  computed: {
    ...mapState(["siterInfo", "language", "classInfo", "config"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  }
};
</script>

<style scoped lang="stylus">
  .menu-list
    .active
      border-bottom: 2px solid #ff6700
    .mdui-container
      display: flex
      justify-content space-between
      flex-wrap: wrap
      padding-top: 77px
      padding-left: 2.4%
      padding-right: 2.4%
      .product
        width: 49%
    .mdui-appbar.mdui-appbar-fixed
      background-color: #fff
      box-shadow none
      border-bottom: 1px solid #f2f2f2
      max-width: 768px;
      margin: 0 auto;
    .mdui-tab.mdui-color-theme
      min-height: 36px
      a
        min-height: 36px
        padding: 0 8px
        min-width: min-content
  .title-img {
    position: absolute;
    left: 45px;
    top:5px
    margin 0 5px
  }
</style>
